import React, {useState} from "react";
import Header from "../components/header";
import Footer from "../components/footer";
import Home from "./home";
import {Redirect, Route, Switch} from "react-router-dom";
import WordBoard from "./wordBoard";
import SentenceBoard from "../components/read/sentence";
import LetterBoard from "./LetterBoard";
import Abcd from "../components/read/abcd";
import Addition from "../components/math/addition";
import Subtraction from "../components/math/subtraction";
import Multiplication from "../components/math/multiplication";
import Division from "../components/math/division";
import CountingNumber from "../components/math/counting-number";
import CountingFigure from "../components/math/counting-figure";

const Main = () => {
    const [selectedConsonant, setSelectedConsonant] = useState("m");

    const handleSelectedConsonant = (consonant) => {
        setSelectedConsonant(consonant);
    }
    return (
        <div className="container-fluid" >
            <Header/>
            <Switch>
                <Route exact path='/home' component={Home}/>
                <Route exact path='/alphabet' component={Abcd}/>
                <Route exact path='/letters' component={() => <LetterBoard selectedConsonant={selectedConsonant}
                                                                           handleSelectedConsonant={handleSelectedConsonant}/>}/>
                <Route exact path='/words' component={() => <WordBoard selectedConsonant={selectedConsonant}
                                                                       handleSelectedConsonant={handleSelectedConsonant}/>}/>
                <Route exact path={'/sentences'} component={() => <SentenceBoard selectedConsonant={selectedConsonant}
                                                                                 handleSelectedConsonant={handleSelectedConsonant}/>}/>
                <Route exact path={'/addition'} component={Addition}/>
                <Route exact path={'/subtraction'} component={Subtraction}/>
                <Route exact path={'/multiplication'} component={Multiplication}/>
                <Route exact path={'/division'} component={Division}/>
                <Route exact path={'/counting'} component={CountingNumber}/>
                <Route exact path={'/countingFigure'} component={CountingFigure}/>
                {/* <Route exact path={'/counting'} component={CountingBoard}/>*/}
                <Redirect to={'/home'}/>
            </Switch>
            <Footer/>
        </div>
    );
}

export default Main;