import React, {useState} from "react";
import Syllable from "./syllable";
import ALPHABET from "../../assets/lessons/alphabet";
import NavButton from "./nav-button";
import AbcdMp3 from "./abcd-mp3";

const Abcd = () => {

    const [activeItem, setActiveItem] = useState(0);
    const [selectedBackground] = useState("white");

    const handleSelectedSyllable = (syllableId) => {
        setActiveItem(syllableId);
    }

    return (
        <div className="container">
            <AbcdMp3/>
            <div className="row justify-content-center m-1">
                {ALPHABET.map((letter) => {
                    return (
                        <div key={letter.id}
                             className={activeItem === letter.id ? "card m-1 flex-grow-1 align-items-center shadow bg-info" : "card m-1 flex-grow-1 align-items-center"}
                             style={{background: "white"}}
                             onClick={() => handleSelectedSyllable(letter.id)}>
                            <div className="card-body bg">
                                <div className="card-text text-center  m-0 px-0 py-0">

                                    <Syllable key={letter.id}
                                              id={letter.id}
                                              letter={letter.name.toUpperCase() + letter.name}
                                              fontSize="2rem"
                                              wordColor={activeItem === letter.id ? selectedBackground : letter.background}/>

                                </div>
                            </div>
                        </div>
                    )
                })}
            </div>
            <NavButton/>
        </div>
    );
}

export default Abcd;