import React, {useEffect, useState} from "react";
import Syllable from "./syllable";
import SENTENCES from "../../assets/lessons/sentences";
import ModalLetter from "./modal";
import NavButton from "./nav-button";

const SentenceBoard = ({selectedConsonant, handleSelectedConsonant}) => {
    const [activeItem, setActiveItem] = useState(0);
    const [selectedBackground] = useState("blue");
    const [counter, setCounter] = useState(1);
    const [actualSentence, setActualSentence] = useState("");

    useEffect(() => {
        setActualSentence(SENTENCES[selectedConsonant] ? SENTENCES[selectedConsonant].sentences[0] : "");
    }, []);

    const nextSentence = () => {
        if (SENTENCES[selectedConsonant] && SENTENCES[selectedConsonant].sentences.length >= 1) {
            setActualSentence(SENTENCES[selectedConsonant].sentences[counter]);
            setCounter(counter + 1);
        }
        if (SENTENCES[selectedConsonant] && counter >= SENTENCES[selectedConsonant].sentences.length - 1) {
            setCounter(0);
        }
    }

    const handleSelectedSyllable = (syllableId) => {
        setActiveItem(syllableId);
    }

    return (
        <div className="container ">
            <ModalLetter handleClick={handleSelectedConsonant} letter={selectedConsonant}/>
            <div className="row justify-content-center ">
                {actualSentence ?
                    <div key={actualSentence.id} className="card m-1 ml-3 mr-3 flex-grow-1 align-items-center ">
                        {
                            process.env.PUBLIC_URL + actualSentence.image ?
                                <img alt="" className="card-img-top p-2" style={{width: "8rem"}}
                                     src={process.env.PUBLIC_URL + actualSentence.image}/> : ""
                        }
                        <div className="card-body">
                            <div className="card-text text-center  m-0 px-0 py-0">
                                {actualSentence.syllables.map((item) => {
                                    return (
                                        <Syllable key={item.id}
                                                  id={item.id}
                                                  letter={item.syllable}
                                                  fontSize={"3rem"}
                                                  wordColor={activeItem === item.id ? selectedBackground : "lightgray"}
                                                  background={"white"}
                                                  handleClick={handleSelectedSyllable}/>
                                    );
                                })}
                            </div>
                        </div>
                    </div>
                    :
                    <div className="card m-1 ml-3 mr-3 flex-grow-1 align-items-center ">
                        <div className="card-body">
                            <div className="card-text text-center  m-0 px-0 py-0">
                                <h4>No se encontraron oraciones para la consonante <span className="font-weight-bold"
                                                                                         style={{fontSize: "3rem"}}>{selectedConsonant}</span>
                                </h4>
                            </div>
                        </div>
                    </div>
                }
            </div>
            <div className="row m-3 m-lg-0 align-items-center justify-content-center align-content-center">
                <button type="button" className="btn btn-info ml-3" onClick={() => nextSentence()}>
                    Siguiente
                </button>
            </div>
            <NavButton/>
        </div>
    );
}

export default SentenceBoard;