function getAmountOfFigure(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min);
}

function getArrayOfFigure(figure, amount) {
    let figures = [];
    for (let i = 1; i <= amount; i++) {
        figures[i] = figure;
    }
    return figures;
}

const MathUtil = {
    getAmountOfFigure,
    getArrayOfFigure
}

export default MathUtil;