import React, {useState} from "react";
import Word from "../components/read/word";
import VOWELS from "../assets/lessons/vowels";
import CONSONANTS from "../assets/lessons/consonant";
import ModalLetter from "../components/read/modal";
import NavButton from "../components/read/nav-button";

const LetterBoard = ({handleSelectedConsonant, selectedConsonant}) => {
    const [consonant] = useState(CONSONANTS[0]);
    const [activeItem, setActiveItem] = useState(0);
    const [selectedBackground] = useState("lightgray");

    const handleClick = (id) => {
        setActiveItem(id)
    }

    return (
        <div className="container">
            <ModalLetter handleClick={handleSelectedConsonant} letter={selectedConsonant}/>
            <div className="row align-items-center">
                <div className="col m-0">
                    <Word letter={selectedConsonant} fontSize={"8rem"} wordColor={"blue"} id={consonant.id}
                          handleClick={handleClick}
                          background={activeItem === consonant.id ? selectedBackground : "white"}
                          border/>
                </div>
                <div className="col m-0">
                    {VOWELS.map((vowel) => {

                        return (
                            <div key={vowel.id + consonant.id} className="row">

                                <Word letter={String.fromCharCode(0x2799)}
                                      fontSize={"3rem"}
                                      wordColor={"red"} background={"white"}
                                      style={{color: "red"}}/>
                                <Word key={vowel.id} letter={vowel.name}
                                      fontSize={"3rem"} wordColor={"gray"}
                                      id={vowel.id} handleClick={handleClick}
                                      background={activeItem === vowel.id ? selectedBackground : "white"}/>
                                <Word letter={String.fromCharCode(61)}
                                      fontSize={"3rem"} wordColor={"green"} background={"white"}/>
                                <Word key={consonant.id + vowel.id} letter={selectedConsonant + vowel.name}
                                      fontSize={"3rem"} wordColor={"gray"}
                                      id={consonant.id + vowel.id} handleClick={handleClick}
                                      background={activeItem === consonant.id + vowel.id ? selectedBackground : "white"}/>
                            </div>
                        );
                    })}
                </div>
            </div>
            <NavButton/>
        </div>
    );
}

export default LetterBoard;