import React from "react";

const Number = ({id, number, numberColor, fontSize, background, handleClick, border}) => {

    return (
        <span className=" text-center  m-0 px-0 py-0"
              style={{color: numberColor, fontSize: fontSize, border: "none"}}
              onClick={handleClick != null ? () => handleClick(id) : () => {
              }}>
            {number}
        </span>
    );
}

export default Number;