import React, {useEffect, useState} from "react";
import MathUtil from "../../util/math-util";
import MathNavButton from "./nav-button-math";
import FIGURES from "./data/html-ascii-code";

const CountingFigure = () => {
    //figures code: https://www.toptal.com/designers/htmlarrows/symbols/
    // HTML and HEX emoji -- https://www.w3schools.com/charsets/ref_emoji.asp
    const [activeItem, setActiveItem] = useState(-1);
    const [figures, setFigures] = useState([]);
    const [figure, setFigure] = useState(FIGURES[0]);
    const handleSelectedFigure = (id) => {
        setActiveItem(id);
    }

    useEffect(() => {
        fillFiguresArray();
    }, []);

    const getFigure = () => {
        setFigure(FIGURES[MathUtil.getAmountOfFigure(0, FIGURES.length - 1)]);
    }

    const fillFiguresArray = () => {
        setActiveItem(-1)
        getFigure();
        setFigures(MathUtil.getArrayOfFigure(figure.hex, MathUtil.getAmountOfFigure(2, 30)));
    }

    return (
        <div className="container">
            <MathNavButton/>
            <div className="row justify-content-center m-1">
                <div className="row col-l-6">
                    <div className={"card m-1 flex-grow-1 align-items-center"}
                         style={{background: "white"}}>
                        <div className="card-body">
                            <div className="card-text text-center  m-0 px-0 py-0">
                                {figures.map((figure, index) => {
                                    return (
                                        <span id={figure} key={index}
                                              className={activeItem === index ? "text-center shadow m-0 p-2 bg-info" : "text-center  m-0 p-2"}
                                              style={{fontSize: "3rem"}}
                                              onClick={() => handleSelectedFigure(index)}
                                        >
                                            {String.fromCodePoint(figure)}
                                        </span>
                                    )
                                })
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row justify-content-center">
                <button className="btn btn-outline-info" onClick={() => fillFiguresArray()}>Siguiente</button>
            </div>
        </div>
    );
}

export default CountingFigure;