import React, {useState} from "react";
import Syllable from "../components/read/syllable";
import WORDS from "../assets/lessons/words";
import ModalLetter from "../components/read/modal";
import NavButton from "../components/read/nav-button";

const WordBoard = ({selectedConsonant, handleSelectedConsonant}) => {
    const [activeItem, setActiveItem] = useState(0);
    const [selectedBackground] = useState("blue");

    const getWordsByConsonant = () => {
        return WORDS.filter((items, index) => {
            if (items.consonant.toString() === selectedConsonant.toString())
                return items;

        })
    }

    const handleSelectedSyllable = (syllableId) => {
        setActiveItem(syllableId);
    }

    return (
        <div className="container ">
            <ModalLetter letter={selectedConsonant} handleClick={handleSelectedConsonant}/>
            <div className="row justify-content-center ">
                {
                    getWordsByConsonant().length > 0 ?

                        getWordsByConsonant()[0].words.map((word) => {
                            return (

                                <div key={word.id} className="card m-1 ml-3 mr-3 flex-grow-1 align-items-center ">
                                    {
                                        process.env.PUBLIC_URL + word.image ?
                                            <img className="card-img-top p-2" style={{width: "8rem"}}
                                                 src={process.env.PUBLIC_URL + word.image} alt={"Not found."}/> : ""
                                    }
                                    <div className="card-body">
                                        <div className="card-text text-center  m-0 px-0 py-0">
                                            {word.syllables.map((item) => {
                                                return (
                                                    <Syllable key={item.id}
                                                              id={item.id}
                                                              letter={item.syllable}
                                                              fontSize={"3rem"}
                                                              wordColor={activeItem === item.id ? selectedBackground : "lightgray"}
                                                              background={"white"}
                                                              handleClick={handleSelectedSyllable}/>
                                                );
                                            })}
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                        :
                        <div className="card m-1 ml-3 mr-3 flex-grow-1 align-items-center ">
                            <div className="card-body">
                                <div className="card-text text-center  m-0 px-0 py-0">
                                    <h4>No se encontraron palabras para la consonante <span className="font-weight-bold"
                                                                                            style={{fontSize: "3rem"}}>{selectedConsonant}</span>
                                    </h4>
                                </div>
                            </div>
                        </div>
                }
            </div>
            <NavButton/>
        </div>
    );
};

export default WordBoard;