import React from "react";

const AlphabetWord = ({letter,id, handleClick}) => {
    return (
        <div className="card m-1 flex-grow-1" data-dismiss="modal" style={{background: "white"}}
             onClick={handleClick != null ? () => handleClick(letter) : () => {
             }}>
            <div className="card-body">
                <h5 className="card-text text-center" style={{color: "blue", fontSize: "3rem"}}>{letter}</h5>
            </div>
        </div>
    );
}

export default AlphabetWord;