import React from "react";

const Word = ({id, letter, wordColor, fontSize, background, handleClick, style, border}) => {
    return (
        <div className="card" style={{background: background, border: border === true ? border : "none"}}
             onClick={handleClick != null ? () => handleClick(id) : () => {
             }}>
            <div className="card-body p-1 m-1">
                <div className="card-text text-center" style={{color: wordColor}}>
                    <span style={{fontSize: fontSize}}>{letter}</span>
                </div>
            </div>
        </div>
    );
}

export default Word;