import React from "react";

const Syllable = ({id, letter, wordColor, fontSize, background, handleClick, style}) => {
    return (
        <span className=" text-center  m-0 px-0 py-0"
              style={{color: wordColor, fontSize: fontSize, background: background, border: "none"}}
              onClick={handleClick != null ? () => handleClick(id) : () => {
              }}>
            {letter}
        </span>
    );
}

export default Syllable;